import React, { useMemo, useRef, useState } from "react";
import styles from "./App.module.scss";
import { useTranslation } from "react-i18next";
import logo from "./images/logo.png";
import blueHeart from "./images/blue-heart.png";
import yellowHeart from "./images/yellow-heart.png";
import cx from "classnames";
import Button from "./components/Button/Button";
import Dialog from "./components/Dialog/Dialog";
import Error from "./components/Error/Error";
import Banner from "./components/Banner/Banner";
import { generateURL } from "./utils";

const MAX_LIMIT = 10000;
const MIN_LIMIT = 5;

function App() {
  const { t } = useTranslation();
  const [isOpenedDialog, setIsOpenedDialog] = useState(false);
  const [amount, setAmount] = useState(10);
  const [isOpenBanner, setIsOpenBanner] = useState(true);
  const [tempAmount, setTempAmount] = useState<number>(0);
  const [error, setError] = useState<string | null>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  const handleCustomAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.currentTarget.value;
    const regexp = /^[1-9](\d{1,9})?$/;

    if (value && !regexp.test(value)) {
      e.preventDefault();
      e.stopPropagation();
      e.target.value = '';
    } else {
      setTempAmount(+value)
    }
  }

  const handleCloseDialog = () => {
    setTempAmount(0)
    setIsOpenedDialog(false);
  }

  const showError = (name: string) => {
    setError(name);
    setTimeout(() => {
      setError(null);
    }, 4000)
  }

  const handleApplyAmount = () => {
    if(inputRef.current) inputRef.current.focus();
    if(tempAmount > MAX_LIMIT) return showError(t('maxLimitError'))
    if(tempAmount < MIN_LIMIT) return showError(t('minLimitError'));
    setAmount(tempAmount);
    handleCloseDialog();
  }

  const isCustomSelected = useMemo(() => {
    const optionsValues = options.map(option => option.amount);
    return !(optionsValues.includes(amount) || !amount);
  }, [amount])

  const handleSubmit = () => {
    if(!amount) return showError(t('Choose your donation first'));

    window.open(generateURL(amount), "_blank");
  }

  const handleDialogKeyDown = (e: any) => {
    if(e.key === 'Enter') return handleApplyAmount();
    if(e.key === 'Escape') return handleCloseDialog();
  }

  return (
    <div className={styles.root}>
      <section className={styles.mainBlock}>
        <div className={styles.logoBlock}>
          <img
            src={blueHeart}
            className={cx(styles.icon, styles.left)}
            alt="logo"
          />
          <img src={logo} className={styles.img} alt="logo" />
          <img
            src={yellowHeart}
            className={cx(styles.icon, styles.right)}
            alt="logo"
          />
        </div>
        <div className={styles.content}>
          <h1 className={styles.title}>{t("title")}</h1>
          <p className={styles.description}>{t("description")}</p>
        </div>
      </section>
      <section className={styles.donate}>
        <div className={styles.card}>
          <h2 className={styles.title}>{t("donateTitle")}</h2>
          <div className={styles.content}>
            <div className={styles.options}>
              {options.map((item) => (
                <div className={styles.option} key={item.amount}>
                  <Button
                    label={`${item.amount}$`}
                    icon={item.icon}
                    check
                    onClick={() => setAmount(item.amount)}
                    type={amount === item.amount ? "secondary" : "primary"}
                  />
                </div>
              ))}
            </div>
            <div className={styles.other}>
              <Button
                className={styles.button}
                type={isCustomSelected ? "secondary" : "primary"}
                check
                icon="edit"
                onClick={() => {
                  setTempAmount(amount);
                  setIsOpenedDialog(true);
                }}
                label={isCustomSelected ? `${amount}$` : t('enterYourAmount')}
              />
            </div>
          </div>
        </div>
      </section>
      <section className={styles.submit}>
        <Button
          disabled={!amount}
          className={styles.button}
          label={t('donate')}
          onClick={handleSubmit}
          type="secondary"
          check
        />
      </section>
      <Dialog isOpen={isOpenedDialog} onClose={handleCloseDialog} title={t('setDonateAmount')}>
        <div className={styles.amountBlock} onKeyDown={handleDialogKeyDown}>
          <label className={styles.inputWrapper}>
            <input ref={inputRef} type="number" autoFocus className={styles.input} onChange={handleCustomAmountChange} value={tempAmount || ''} placeholder="100"/>
            <span className={cx(styles.currency, {[styles.active]: !!tempAmount})}>$</span>
          </label>
          <div className={styles.amountBlockFooter}>
            <Button
              disabled={!tempAmount}
              className={styles.button}
              onClick={handleApplyAmount}
              label={t('done')}
              type={!!tempAmount ? "secondary" : "primary"}
            />
          </div>
        </div>
      </Dialog>
      <Error error={error} onClick={() => setError(null)}/>
      <Banner isOpen={isOpenBanner} onClick={() => setIsOpenBanner(false)} />
    </div>
  );
}

export default App;

const options = [
  {
    amount: 5,
    icon: "icon1",
  },
  {
    amount: 10,
    icon: "icon2",
  },
  {
    amount: 20,
    icon: "icon3",
  },
  {
    amount: 50,
    icon: "icon4",
  },
  {
    amount: 100,
    icon: "icon5",
  },
];