import React from "react";
import styles from "./Banner.module.scss";
import { AnimatePresence, motion } from 'framer-motion'
import adImage1 from '../../images/help-1.png'
import adImage2 from '../../images/help-2.png'
import adImage3 from '../../images/help-3.png'

const IMAGES = [{url: adImage1, color: "#0054cc"}, {url: adImage2, color: "#ffe202"}, {url: adImage3, color: "#d3696f"}]

function getRandomInt(max: number) {
  return Math.floor(Math.random() * max);
}

const imageIndex =  getRandomInt(IMAGES.length);

interface IProps {
  isOpen: boolean;
  onClick?: () => void;
}

const Banner = ({ isOpen, onClick }: IProps) => {
  const url = IMAGES[imageIndex] ? IMAGES[imageIndex].url : IMAGES[0].url;

  return (
    <AnimatePresence>
      {!!isOpen && (
        <motion.div
          className={styles.root}
          initial={{ opacity: 1 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={onClick}
        >
          <img className={styles.img} src={url} alt={`Banner ${imageIndex + 1}`} />
        </motion.div>
      )}
    </AnimatePresence>
  );
};

export default Banner;
