import * as React from "react";
import styles from "./Button.module.scss";
import cx from "classnames";
import { motion } from "framer-motion";
import yellowHeartIcon from "../../images/yellow-heart.png";
import edit from "../../images/edit.png";
import icon1 from "../../images/1.png";
import icon2 from "../../images/2.png";
import icon3 from "../../images/3.png";
import icon4 from "../../images/4.png";
import icon5 from "../../images/5.png";

const ICONS: Record<string, string> = {
  yellowHeart: yellowHeartIcon,
  edit: edit,
  icon1: icon1,
  icon2: icon2,
  icon3: icon3,
  icon4: icon4,
  icon5: icon5,
};

interface IProps {
  label: string;
  className?: string;
  radius?: number;
  type?: "primary" | "secondary" | "gradient";
  icon?: string;
  disabled?: boolean;
  onClick?: () => void;
  check?: boolean;
}

const Button = ({
  label,
  icon,
  className = "",
  radius = 20,
  type = "primary",
  disabled,
  check,
  onClick,
}: IProps) => {

  return (
    <motion.div
      className={cx(styles.root, className, {
        [styles.primary]: type === "primary",
        [styles.secondary]: type === "secondary",
        [styles.gradient]: type === "gradient",
        [styles.check]: check,
        [styles.disabled]: disabled
      })}
      style={{ borderRadius: radius }}
      whileTap={{ scale: 0.95 }}
      onClick={onClick}
    >
      <span className={styles.label}>{label}</span>
      {!!icon && !!ICONS[icon] && (
        <img src={ICONS[icon]} className={styles.icon} alt={`${label}`} />
      )}
    </motion.div>
  );
};

export default Button;
